<template>
    <BaseModal
        name="track-kebab"
        :mobileMode="true"
        :closeOutside="true"
        :isLoading="data.loading"
    >
        <template v-slot:title>
            <div class="title">
                <div class="img" @click="gallery">
                    <Img v-if="data.item?.cover?.resized"
                        :preview="data.item?.cover?.resized"
                        :original="data.item?.cover?.original"
                        :alt="data.item?.title"
                        aspectRatio="1 / 1"
                    />
                </div>
                <span>{{ data.item?.title }}</span>
            </div>
        </template>
        <template v-slot:content>
            <ul class="kebab-list">
                <li class="">
                    <div class="buttons">
                        <IconButton
                            icon="download"
                            class="tertiary size-xl"
                            @click="download"
                        />

                        <AudioToggle v-if="data.item?.wave"
                            :state="state"
                            :light="true"
                            :class="{ invert: others.theme !== 'dark' }"
                            class="secondary size-s"
                            @play="play"
                            @pause="pause"
                        />

                        <IconButton
                            icon="heart"
                            class="tertiary size-xl"
                            @click="like"
                        />
                    </div>
                </li>
                <li v-if="data.item?.trackFile">
                    <TrackWave 
                        :wave="data.item.wave"
                        :id="data.item.id"
                        :duration="data.item.duration"
                        :simplify="2"
                        class="wave"
                        @change="seek"
                    />
                </li>
                <li class="clickable" @click="shareIt">
                    <Icon icon="share"/>
                    <span>{{ t('modals.track-kebab.share') }}</span>
                </li>

                <li class="clickable" @click="about">
                    <Icon icon="help"/>
                    <span>{{ t('modals.track-kebab.about') }}</span>
                </li>

                <template v-if="data.releases?.length">
                    <li v-for="item in data.releases"
                        :key="item.id"
                        class="clickable"
                        @click="openRelease(item)">

                        <Img
                            v-if="item?.cover"
                            :preview="item?.cover?.crop"
                            :original="item?.cover?.original"
                            :alt="item.title"
                            :skeleton="true"
                        />
                        <Icon v-else icon="playlist"/>

                        <span>{{ t('modals.track-kebab.go-to-release', { 'release-title': item.title }) }}</span>
                    </li>
                </template>

                <template v-if="contributors?.length">
                    <li v-for="item in contributors"
                        :key="item.id"
                        class="clickable"
                        @click="openArtist(item)">

                        <Img
                            v-if="item?.profileImage"
                            :preview="item?.profileImage?.crop"
                            :original="item?.profileImage?.original"
                            :alt="item.name"
                            :skeleton="true"
                        />
                        <Icon v-else icon="artists"/>

                        <span>{{ item.name }}</span>
                    </li>
                </template>

            </ul>
        </template>
    </BaseModal>
</template>

<script setup>
import { onMounted, reactive, computed } from 'vue';
import { useI18n } from 'vue-i18n';

import Img from 'ðŸ”—/components/ui/Img.vue';
import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
import BaseModal from 'ðŸ”—/components/modals/BaseModal.vue';
import TrackWave from 'ðŸ”—/components/ui/TrackWave.vue';
import AudioToggle from 'ðŸ”—/components/ui/AudioToggle.vue';
import stores from '@/stores';

import api from 'ðŸ”—/api';
import { message } from 'ðŸ”—/utils/utils';

const { t } = useI18n();

const tg = stores.tg();
const modals = stores.modals();
const share = stores.share();
const player = stores.player();
const nav = stores.nav();
const others = stores.others();

const data = reactive({
    loading: true,
    releases: []
});

const state = computed(() => {
    if (!player.panes.default || player.panes.default._item?.id !== data.item?.id) {
        return;
    }

    return player.panes.default._state;
});

const seek = async (position) => {
    if (state.value !== 'playing' && state.value !== 'stopped') {
        await play();
        setTimeout(() => {
            if (player.panes.default) {
                player.panes.default.seek(position || 0);
            }
        }, 2000);
    }
}

const play = async () => {
    if (!state.value || state.value === 'stopped') {
        await player.track(data.item);

        if (!player.panes?.default) {
            return;
        }

        player.panes.default.play();
        return;
    }

    if (player.panes.default && state.value === 'paused') {
        player.panes.default.resume();
        return;
    }
}

const pause = async () => {
    if (!player.panes?.default) {
        return;
    }

    if (state.value === 'playing') {
        player.panes.default.pause();
        return;
    }
}


const shareIt = () => {
    share.track(data.item, { type: 'release', release: data.releases[0] });

    close();
}

const about = () => {
    close();

    modals.open('track', { quietClose: true, data: {
        id: data.item.id,
        item: data.item,
        releases: data.releases || []
    } });
}

const openRelease = (release) => {
    nav.open('release', { id: release?.id });
    close();
}

const openArtist = (artist) => {
    nav.open('artist', { code: artist?.code });
    close();
}

const download = () => {
    close();

    modals.open('track-download', { quietClose: true, data: {
        id: data.item.id,
        item: data.item,
        releases: data.releases || []
    } });
}

const gallery = () => {
    if (!data.item?.id || !data.item?.cover?.original) {
        return;
    }

    modals.open('gallery', { data: { imgs: [{
        title: data.item.title,
        ...data.item?.cover
    }] } });
}

const like = () => {
    message.info(t('common.soon'));
}

const close = () => {
    modals.close('track-kebab');
};

// Contributors
    const contributors = computed(() => {
        if (!data.item?.contributors?.length) {
            return [];
        }

        return data.item.contributors.filter(item => (item.role === 'PRIMARY' || item.role === 'FEATURED')
            && item.state === 'CONFIRMED').map(item => item.artist);
    });

onMounted(async () => {
    const payload = modals.getData('track-kebab');

    if (!payload?.id) {
        close();
        return;
    }

    if (payload.item) {
        data.item = payload.item;
    } else {
        data.item = await api.tracks().get(payload.id);
    }

    if (payload.releases) {
        data.releases = payload.releases;
    } else {
        const releases = await api.releases().list({ tracks: [payload.id] });
        if (releases?.items) {
            data.releases = releases.items;
        }
    }

    data.loading = false;
});

</script>

<style lang="scss" scoped>
    .title {
        display: flex;
        width: 100%;
        align-items: center;
        gap: 24px;
        flex-direction: column;
        padding-top: 12px;

        .img {
            width: 74px;
            height: 74px;
            border-radius: 12px;
            @include animage-booble-shadow;

            img {
                width: 100%;
                height: 100%;
                border-radius: 12px;
            }
        }
    }

    .buttons {
        display: flex;
        flex-direction: row;
        gap: 8px;
        align-items: center;
        justify-content: center;
    }

    ul.kebab-list {
        @include settings-list;
        margin-top: 24px;
        li {
            .skeleton-wrapper {
                min-width: 34px;
                max-width: 34px;
                max-height: 34px;
                width: 34px;
                height: 34px;
                margin: -7px;
                border-radius: 8px;
                overflow: hidden;
            }
            .wave {
                height: 80px;
            }
        }
    }
</style>
